import ShowBlogs from './components/ShowBlog.vue'
import AddBlog from './components/AddBlog.vue'
import SingleBolg from './components/SingleBolg.vue'

export default[
    {
        path: '/',
        component: ShowBlogs
    },
    {
        path: '/add',
        component: AddBlog
    },
    {
        path: '/blog/:id',
        component: SingleBolg
    }
]


